.headerWrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 50px;
  padding: 0 20px;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 200;
  background-color: white;
  box-shadow: 0 5px 10px lightgray;
}

.logo {
  font-size: 28px;
  color: red;
  font-weight: bold;
}